<template>
  <div>
    <el-row :gutter="30">
      <el-alert
        v-if="alert.show"
        :title="alert.title"
        :type="alert.type"
        show-icon
        style="margin-bottom: 2vh" 
        @close="alert.show = false" />
      <pre>{{ alert.result }}</pre>
      
      <el-form 
        :model="form" 
        :rules="rules" 
        :label-position="labelPosition" 
        ref="OCDAutoForm"
        label-width="200px" 
        size="mini">
        <el-col :xs="24" :sm="12" :md="8" :lg="8" :xl="8">
          <el-form-item label="Không có dữ liệu">
            <el-switch
              v-model="form.value"
              active-color="#13ce66"
              inactive-color="#ff4949">
            </el-switch>
          </el-form-item>
          
          <el-form-item label="Giá trị định giá" prop="valuation_value">
            <el-input 
              v-model.number="form.valuation_value"
              clearable>
              <template slot="append">đồng</template>
            </el-input>
          </el-form-item>
          
          <el-form-item label="NV đang đảm bảo" prop="obligations_guaranteed">
            <el-input 
              v-model.number="form.obligations_guaranteed"
              clearable>
              <template slot="append">đồng</template>
            </el-input>
          </el-form-item>
        </el-col>

        <el-col v-if="!form.value" :xs="24" :sm="12" :md="8" :lg="8" :xl="8">
          <el-form-item label="Tên dự án" prop="project_name">
            <el-select v-model="form.project_name">
              <el-option label="Dự án 1" value="CĐT 1" />
              <el-option label="Dự án 2" value="CĐT 2" />
              <el-option label="Dự án 3" value="CĐT 3" />
            </el-select>
          </el-form-item>

          <el-form-item label="Loại đất nền" prop="ground_type">
            <el-select v-model="form.ground_type">
              <el-option label="Loại 1" value="Loại 1" />
              <el-option label="Loại 2" value="Loại 2" />
              <el-option label="Loại 3" value="Loại 3" />
            </el-select>
          </el-form-item>

          <el-form-item label="Diện tích đất" prop="surface">
            <el-input 
              v-model.number="form.surface"
              clearable>
              <template slot="append">m2</template>
            </el-input>
          </el-form-item>
        </el-col>

        <el-col v-if="!form.value" :xs="24" :sm="12" :md="8" :lg="8" :xl="8">
          <el-form-item label="Tên chủ đầu tư">
            <span>{{ form.investor }}</span>
          </el-form-item>
          <el-form-item label="Tổng diện tích sàn XD" prop="construction_surface">
            <el-input 
              v-model.number="form.construction_surface"
              clearable>
              <template slot="append">m2</template>
            </el-input>
          </el-form-item>
        </el-col>
      </el-form>
    </el-row>
    <el-button plain type="primary" size="small" class="ml-4" @click="OCD('OCDAutoForm')">Test ISV đất nền</el-button>
  </div>
</template>

<script>
export default {
  name: 'ISV-ground',

  mounted() {
    // this.initReferenceData()
  },

  data: () => ({
    labelPosition: 'left',
    // Dataset
    dataset: {},

    form: {
      value: false,
      valuation_value: null,
      obligations_guaranteed: null,
      project_name: null,
      investor: null,
      ground_type: null,
      surface: null,
      construction_surface: null
    },

    rules: {
      valuation_value: [{ required: true, message: 'Vui lòng chọn ', trigger: 'blur' }],
      obligations_guaranteed: [{ required: true, message: 'Vui lòng chọn ', trigger: 'blur' }],
      project_name: [{ required: true, message: 'Vui lòng chọn ', trigger: 'change' }],
      ground_type: [{ required: true, message: 'Vui lòng chọn ', trigger: 'change' }],
      surface: [{ required: true, message: 'Vui lòng chọn ', trigger: 'blur' }],
      construction_surface: [{ required: true, message: 'Vui lòng chọn ', trigger: 'blur' }]
    },
    // Alert
    alert: {
      show: false,
      title: null,
      type: null,
      result: null
    }
  }),

  watch: {
    'form.project_name'(val) {
      this.investor = val
    }
  },

  methods: {
    initReferenceData() {
      // OCD O
      this.$services.referenceData.get({ params: { dataset: 'OCD_auto' } })
        .then(res => this.dataset = res.data)
    },

    OCD(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.$services.OCD.post('auto', this.form)
            .then(res => {
              this.alert.show   = false
              this.alert.result = res.data
            })
            .catch(err => {
              this.alert.show  = true
              this.alert.type  = 'error'
              this.alert.title = err
            })
        } else {
          this.alert.show  = true
          this.alert.type  = 'error'
          this.alert.title = 'Error: invalid form'
          return false
        }
      })
    }
  }
}
</script>



