<template>
  <div>
    <el-row :gutter="30">
      <el-alert
        v-if="alert.show"
        :title="alert.title"
        :type="alert.type"
        show-icon
        style="margin-bottom: 2vh" 
        @close="alert.show = false" />
      <pre>{{ alert.result }}</pre>
      
      <el-form 
        :model="form" 
        :rules="rules" 
        :label-position="labelPosition" 
        ref="OCDAutoForm"
        label-width="200px" 
        size="mini">
        <el-col :xs="24" :sm="12" :md="8" :lg="8" :xl="8">
          <el-form-item label="Giá trị định giá" prop="valuation_value">
            <el-input 
              v-model.number="form.valuation_value"
              clearable>
              <template slot="append">đồng</template>
            </el-input>
          </el-form-item>
          
          <el-form-item label="NV đang đảm bảo" prop="obligations_guaranteed">
            <el-input 
              v-model.number="form.obligations_guaranteed"
              clearable>
              <template slot="append">đồng</template>
            </el-input>
          </el-form-item>
        </el-col>

        <el-col v-if="!form.value" :xs="24" :sm="24" :md="8" :lg="8" :xl="8">
          <el-form-item label="Tình trạng xe" prop="status">
            <el-radio-group v-model="form.status">
              <el-radio-button label="new">Cũ</el-radio-button>
              <el-radio-button label="old">Mới</el-radio-button>
            </el-radio-group>
          </el-form-item>

          <el-form-item label="Loại xe" prop="type">
            <el-radio-group v-model="form.type">
              <el-radio-button label="Xe tải">Xe du lịch</el-radio-button>
              <el-radio-button label="Xe du lịch">Xe tải</el-radio-button>
              <el-radio-button label="Xe khách">Xe khách</el-radio-button>
            </el-radio-group>
          </el-form-item>

          <el-form-item label="Nơi sản xuất" prop="location">
            <el-radio-group v-model="form.location">
              <el-radio-button label="VN">Việt Nam</el-radio-button>
              <el-radio-button label="other">Khác</el-radio-button>
            </el-radio-group>
          </el-form-item>

          <el-form-item prop="diff">
            <label slot="label">
              Số năm chênh lệch
              <el-tooltip effect="dark" placement="top">
                <div slot="content">
                  Độ chênh giữa năm bắt đầu sử dụng và năm sản xuất
                </div>
                <i class="el-icon-warning" />
              </el-tooltip>
            </label>
            <el-radio-group v-model="form.diff">
              <el-radio-button label="0" value="0" />
              <el-radio-button label="1" value="1" />
              <el-radio-button label="2" value="2" />
              <el-radio-button label="3" value="3" />
            </el-radio-group>
          </el-form-item>

          <el-form-item label="Thông tin xe" prop="detail">
            <el-cascader v-model="form.detail" :options="cascader">
              <template slot-scope="{ node, data }">
                <span>{{ data.label }}</span>
                <span v-if="!node.isLeaf"> ({{ data.children.length }}) </span>
              </template>
            </el-cascader>
          </el-form-item>

          <el-form-item label="Số chỗ" prop="seat">
            <el-input-number v-model.number="form.seat" controls-position="right" :min="0" />
          </el-form-item>

          <el-form-item prop="weight">
            <label slot="label">
              Tải trọng
              <el-tooltip effect="dark" placement="top">
                <div slot="content">
                  Đơn vị tấn
                </div>
                <i class="el-icon-warning" />
              </el-tooltip>
            </label>
            <el-input-number v-model.number="form.weight" controls-position="right" :min="0" />
          </el-form-item>
        </el-col>

        <el-col v-if="!form.value" :xs="24" :sm="24" :md="8" :lg="8" :xl="8">
          <el-form-item label="Giá trị HĐMB xe" prop="contract_value">
            <el-input v-model.number="form.contract_value">
              <template slot="append">đồng</template>
            </el-input>
          </el-form-item>

          <el-form-item label="Năm sản xuất" prop="year">
            <el-input-number v-model.number="form.year" controls-position="right" :min="1900" :max="new Date().getFullYear()" />
          </el-form-item>

          <el-form-item label="Số Km đã đi" prop="km">
            <el-input-number v-model.number="form.km" controls-position="right" :min="0" />
          </el-form-item>

          <el-form-item label="Số khung" prop="frame_code">
            <el-input v-model="form.frame_code" />
          </el-form-item>

          <el-form-item label="Số máy" prop="machine_code">
            <el-input v-model="form.machine_code" />
          </el-form-item>

          <el-form-item label="Biển kiểm soát" prop="license_plate">
            <el-input v-model="form.license_plate" />
          </el-form-item>
        </el-col>
      </el-form>
    </el-row>
    <el-button plain type="primary" size="small" class="ml-4" @click="OCD('OCDAutoForm')">Test ISV đất nền</el-button>
  </div>
</template>

<script>
import { cascader } from '@/api/data_sample'

export default {
  name: 'ISV-ground',

  mounted() {
    // this.initReferenceData()
  },

  data: () => ({
    labelPosition: 'left',
    // Dataset
    cascader: cascader,

    dataset: {},

    form: {
      valuation_value: null,
      obligations_guaranteed: null,
      status: null,
      type: null,
      location: null,
      detail: null,
      seat: null,
      diff: null,
      weight: null,
      contract_value: null,
      year: null,
      km: null,
      frame_code: null,
      machine_code: null,
      license_plate: null
    },

    rules: {
      valuation_value: [{ required: true, message: 'Vui lòng chọn ', trigger: 'blur' }],
      obligations_guaranteed: [{ required: true, message: 'Vui lòng chọn ', trigger: 'blur' }],
      status: [{ required: true, message: 'Vui lòng chọn ', trigger: 'change' }],
      type: [{ required: true, message: 'Vui lòng chọn ', trigger: 'change' }],
      location: [{ required: true, message: 'Vui lòng chọn ', trigger: 'change' }],
      detail: [{ required: true, message: 'Vui lòng chọn ', trigger: 'change' }],
      seat: [{ required: true, message: 'Vui lòng nhập ', trigger: 'blur' }],
      diff: [{ required: true, message: 'Vui lòng nhập ', trigger: 'blur' }],
      contract_value: [{ required: true, message: 'Vui lòng nhập ', trigger: 'blur' }],
      year: [{ required: true, message: 'Vui lòng nhập ', trigger: 'blur' }],
      km: [{ required: true, message: 'Vui lòng nhập ', trigger: 'blur' }],
      frame_code: [{ required: true, message: 'Vui lòng nhập ', trigger: 'blur' }]
    },
    // Alert
    alert: {
      show: false,
      title: null,
      type: null,
      result: null
    }
  }),

  methods: {
    initReferenceData() {
      // OCD O
      this.$services.referenceData.get({ params: { dataset: 'OCD_auto' } })
        .then(res => this.dataset = res.data)
    },

    OCD(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.$services.OCD.post('auto', this.form)
            .then(res => {
              this.alert.show   = false
              this.alert.result = res.data
            })
            .catch(err => {
              this.alert.show  = true
              this.alert.type  = 'error'
              this.alert.title = err
            })
        } else {
          this.alert.show  = true
          this.alert.type  = 'error'
          this.alert.title = 'Error: invalid form'
          return false
        }
      })
    }
  }
}
</script>
<style lang="css" scoped>
>>>.el-cascader--mini {
  width: 100%;
}
</style>



