<template>
  <div>
    <el-row :gutter="30">
      <el-alert
        v-if="alert.show"
        :title="alert.title"
        :type="alert.type"
        show-icon
        style="margin-bottom: 2vh" 
        @close="alert.show = false" />
      <pre>{{ alert.result }}</pre>
      
      <el-form 
        :model="form" 
        :rules="rules" 
        :label-position="labelPosition" 
        ref="OCDSalaryForm"
        label-width="200px" 
        size="mini">
        <el-col :xs="24" :sm="12" :md="8" :lg="8" :xl="8">
          <el-form-item label="Tên cơ quan" prop="work_place_name">
            <el-select
              v-model="form.work_place_name"
              filterable
              remote
              allow-create
              reserve-keyword
              :remote-method="remoteMethod"
              :loading="loading">
              <el-option
                v-for="(item, i) in options"
                :key="i"
                :label="item.label"
                :value="item.id">
              </el-option>
            </el-select>
          </el-form-item>

          <el-form-item v-if="!isExisted" label="Nhóm cơ quan" prop="work_place_group">
            <el-select v-model="form.work_place_group">
              <el-option 
                v-for="(o, i) in dataset.work_place_group" 
                :key="i"
                :label="o.label" 
                :value="o.id" />
            </el-select>
          </el-form-item>

          <el-form-item label="Địa bàn cơ quan" prop="work_place_area">
            <el-select v-model="form.work_place_area">
              <el-option 
                v-for="(o, i) in dataset.work_place_area" 
                :key="i"
                :label="o.label" 
                :value="o.id" />
            </el-select>
          </el-form-item>

          <el-form-item label="Chuyên môn nghề nghiệp" prop="job_speciality">
            <el-select v-model="form.job_speciality">
              <el-option 
                v-for="(o, i) in dataset.job_speciality" 
                :key="i"
                :label="o.label" 
                :value="o.id" />
            </el-select>
          </el-form-item>

          <el-form-item v-if="isPrivateCompany" label="Ngành nghề kinh doanh" prop="business_field">
            <el-select v-model="form.business_field">
              <el-option 
                v-for="(o, i) in dataset.business_field" 
                :key="i"
                :label="o.label" 
                :value="o.id" />
            </el-select>
          </el-form-item>

          <el-form-item v-if="otherIncome" label="Số giờ làm thêm/tháng" prop="overtime_hours_per_month">
            <el-select v-model="form.overtime_hours_per_month">
              <el-option 
                v-for="(o, i) in dataset.overtime_hours_per_month" 
                :key="i"
                :label="o.label" 
                :value="o.id" />
            </el-select>
          </el-form-item>
        </el-col>

        <el-col :xs="24" :sm="12" :md="8" :lg="8" :xl="8">
          <el-form-item label="Vị trí công tác" prop="job_position">
            <el-select v-model="form.job_position">
              <el-option 
                v-for="(o, i) in dataset.job_position" 
                :key="i"
                :label="o.label" 
                :value="o.id" />
            </el-select>
          </el-form-item>

          <el-form-item label="Trình độ học vấn" prop="learning_level">
            <el-select v-model="form.learning_level">
              <el-option 
                v-for="(o, i) in dataset.learning_level" 
                :key="i"
                :label="o.label" 
                :value="o.id" />
            </el-select>
          </el-form-item>

          <el-form-item label="Loại HĐLĐ" prop="labor_contract_type">
            <el-select v-model="form.labor_contract_type">
              <el-option 
                v-for="(o, i) in dataset.labor_contract_type" 
                :key="i"
                :label="o.label" 
                :value="o.id" />
            </el-select>
          </el-form-item>
        </el-col>

        <el-col :xs="24" :sm="12" :md="8" :lg="8" :xl="8">
          <el-form-item prop="past_seniority">
            <label slot="label">TNCT trong quá khứ
              <el-tooltip effect="dark" placement="top">
                <div slot="content">
                  Thâm niên công tác không tính thời gian công tác tại đơn vị hiện tại
                </div>
                <i class="el-icon-warning" />
              </el-tooltip>
            </label>
            <el-select v-model="form.past_seniority">
              <el-option 
                v-for="(o, i) in dataset.past_seniority" 
                :key="i"
                :label="o.label" 
                :value="o.id" />
            </el-select>
          </el-form-item>

          <el-form-item prop="current_seniority">
            <label slot="label">TNCT hiện tại
              <el-tooltip effect="dark" placement="top">
                <div slot="content">Thời gian công tác tại đơn vị hiện tại</div>
                <i class="el-icon-warning" />
              </el-tooltip>
            </label>
            <el-select v-model="form.current_seniority">
              <el-option 
                v-for="(o, i) in dataset.current_seniority" 
                :key="i"
                :label="o.label" 
                :value="o.id" />
            </el-select>
          </el-form-item>

          <el-form-item label="Hình thức nhận thu nhập" prop="income_receiving_type">
            <el-select v-model="form.income_receiving_type">
              <el-option 
                v-for="(o, i) in dataset.income_receiving_type" 
                :key="i"
                :label="o.label" 
                :value="o.id" />
            </el-select>
          </el-form-item>
        </el-col>
      </el-form>
    </el-row>
    <el-button plain type="primary" class="ml-4" size="small" @click="OCD('OCDSalaryForm')">Test OCD lương</el-button>
  </div>
</template>

<script>
export default {
  name: 'OCD-salary',

  created() {
    this.initReferenceData()
  },

  data: () => ({
    labelPosition: 'left',
    // Dataset
    dataset: {},

    form: {
      work_place_name: null,
      work_place_group: null,
      work_place_area: null,
      business_field: null,
      overtime_hours_per_month: null,
      job_speciality: null,
      job_position: null,
      learning_level: null,
      past_seniority: null,
      current_seniority: null,
      labor_contract_type: null,
      income_receiving_type: null
    },

    rules: {
      work_place_name          : [{ required: true, message: 'Vui lòng chọn ', trigger: 'change' }],
      work_place_group         : [{ required: true, message: 'Vui lòng chọn ', trigger: 'change' }],
      work_place_area          : [{ required: true, message: 'Vui lòng chọn ', trigger: 'change' }],
      business_field           : [{ required: true, message: 'Vui lòng chọn ', trigger: 'change' }],
      overtime_hours_per_month : [{ required: true, message: 'Vui lòng chọn ', trigger: 'change' }],
      job_speciality           : [{ required: true, message: 'Vui lòng chọn ', trigger: 'change' }],
      job_position             : [{ required: true, message: 'Vui lòng chọn ', trigger: 'change' }],
      learning_level           : [{ required: true, message: 'Vui lòng chọn ', trigger: 'change' }],
      past_seniority           : [{ required: true, message: 'Vui lòng chọn ', trigger: 'change' }],
      current_seniority        : [{ required: true, message: 'Vui lòng chọn ', trigger: 'change' }],
      labor_contract_type      : [{ required: true, message: 'Vui lòng chọn ', trigger: 'change' }],
      income_receiving_type    : [{ required: true, message: 'Vui lòng chọn ', trigger: 'change' }]
    },
    // Work place type
    loading: false,
    options: [],
    // Alert
    alert: {
      show: false,
      title: null,
      type: null,
      result: null
    }
  }),

  computed: {
    isExisted() {
      const wpList = this.dataset.work_place_name
      const wpName = this.form.work_place_name

      if (wpList && wpName !== null) {
        return wpList.findIndex(x => x.id === wpName) !== -1
      }
      return true
    },

    otherIncome() {
      const wpList      = this.dataset.work_place_name
      const wpGroupList = this.dataset.work_place_group
      const jsList      = this.dataset.job_speciality

      const wpId      = this.form.work_place_name
      const wpGroupId = this.form.work_place_group
      const jsID      = this.form.job_speciality

      if (wpList && wpId !== null) {
        const wp  = wpList.find(x => x.id === wpId)
        const wpG = wpGroupList.find(x => x.id === wpGroupId)
        const js  = jsList.find(x => x.id === jsID)

        if (wp) {
          if (wp.group_oc === '1') {
            this.dataset.job_position = this.dataset.job_position_goverment
            return true
          } else {
            this.dataset.job_position = this.dataset.job_position_company
          }
        }
        if (wpG) { 
          if (wpG.group_oc === '1') {
            this.dataset.job_position = this.dataset.job_position_goverment
            return true
          } else {
            this.dataset.job_position = this.dataset.job_position_company
          }
        }
        if (js) if (js.other_income) return true
      }
    },

    isPrivateCompany() {
      const wpList      = this.dataset.work_place_name
      const wpGroupList = this.dataset.work_place_group
      const wpId        = this.form.work_place_name
      const wpGroupId   = this.form.work_place_group

      if (wpList && wpId !== null) {
        const wp  = wpList.find(x => x.id === wpId)
        const wpG = wpGroupList.find(x => x.id === wpGroupId)

        if (wp) if (wp.is_private_company) return true
        if (wpG) if (wpG.is_private_company) return true
      }
    }
  },

  watch: {
    'form.work_place_name'() {
      this.form.work_place_group = null
      this.form.overtime_hours_per_month = null
      this.form.business_field = null
    },

    'form.work_place_group'() {
      this.form.overtime_hours_per_month = null
      this.form.business_field = null
    },

    'form.job_speciality'() {
      this.form.overtime_hours_per_month = null
    }
  },

  methods: {
    initReferenceData() {
      this.$services.iDecisionReferenceData.get({ params: { dataset: 'OCD_salary' } })
        .then(res => this.dataset = res.data)
    },

    remoteMethod(query) {
      if (query !== '') {
        this.loading = true
        setTimeout(() => {
          this.loading = false
          this.options = this.dataset.work_place_name.filter(item => {
            return item.label.toLowerCase()
              .indexOf(query.toLowerCase()) > -1
          })
        }, 50)
      } else {
        this.options = []
      }
    },

    OCD(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.$services.iDecisionOCD.post('salary', this.form)
            .then(res => {
              this.alert.show   = false
              this.alert.result = res.data
            })
            .catch(err => {
              this.alert.show  = true
              this.alert.type  = 'error'
              this.alert.title = err
            })
        } else {
          this.alert.show  = true
          this.alert.type  = 'error'
          this.alert.title = 'Error: invalid form'
          return false
        }
      })
    }
  }
}
</script>



