<template>
  <div>
    <el-row :gutter="30">
      <el-alert
        v-if="alert.show"
        :title="alert.title"
        :type="alert.type"
        show-icon
        style="margin-bottom: 2vh" 
        @close="alert.show = false" />
      <pre>{{ alert.result }}</pre>
      
      <el-form 
        :model="form" 
        :rules="rules" 
        :label-position="labelPosition" 
        ref="ISVApartmentForm"
        label-width="200px" 
        size="mini">
        <el-col :xs="24" :sm="12" :md="8" :lg="8" :xl="8">
          <el-form-item label="Tên dự án" prop="project_name">
            <el-select v-model="form.project_name" filterable allow-create>
              <el-option 
                v-for="(p, i) in dataset.project" 
                :key="i"
                :label="p.project_name" 
                :value="p.id" />
            </el-select>
          </el-form-item>

          <el-form-item v-if="!isExistedProject" label="Phân khúc" prop="segment">
            <el-radio-group v-model="form.segment" size="mini">
              <el-radio-button 
                v-for="(p, i) in dataset.segment" 
                :key="i"
                :label="p.id">
                {{ p.segment }}
              </el-radio-button>
            </el-radio-group>
          </el-form-item>

          <el-form-item v-if="!isExistedProject" label="Năm xây dựng" prop="construction_year">
            <el-input-number 
              v-model.number="form.construction_year" 
              :min="0" 
              controls-position="right" />
          </el-form-item>

          <el-form-item prop="area">
            <label slot="label">Diện tích căn hộ
              <el-tooltip effect="dark" placement="top">
                <div slot="content">
                  Đơn vị m2
                </div>
                <i class="el-icon-warning" />
              </el-tooltip>
            </label>
            <el-input-number 
              v-model.number="form.area" 
              :min="0" 
              controls-position="right" />
          </el-form-item>
        </el-col>
      </el-form>
    </el-row>
    <el-button plain type="primary" size="small" class="ml-4" @click="ISV('ISVApartmentForm')">Test ISV căn hộ</el-button>
  </div>
</template>

<script>
export default {
  name: 'ISV-ground',

  mounted() {
    this.initReferenceData()
  },

  data: () => ({
    labelPosition: 'left',
    // Dataset
    dataset: {},

    form: {
      description: 'BĐS chung cư',
      type: 'apartment',
      valuation_value: null,
      obligations_guaranteed: null,
      project_name: null,
      segment: null,
      construction_year: null,
      area: null
    },

    rules: {
      valuation_value: [{ required: true, message: 'Vui lòng nhập ', trigger: 'blur' }],
      obligations_guaranteed: [{ required: true, message: 'Vui lòng nhập ', trigger: 'blur' }],
      project_name: [{ required: true, message: 'Vui lòng chọn ', trigger: 'change' }],
      segment: [{ required: true, message: 'Vui lòng nhập ', trigger: 'blur' }],
      construction_year: [{ required: true, message: 'Vui lòng nhập ', trigger: 'blur' }],
      area: [{ required: true, message: 'Vui lòng nhập ', trigger: 'blur' }]
    },
    isExistedProject: true,
    // Alert
    alert: {
      show: false,
      title: null,
      type: null,
      result: null
    }
  }),

  watch: {
    'form.project_name'(project_id) {
      if (this.dataset !== {}) {
        const projects = this.dataset.project
        this.isExistedProject = projects.findIndex(p => p.id === project_id) !== -1
      } else {
        this.isExistedProject = true
      }
      this.segment = null
      this.construction_year = null
    }
  },

  methods: {
    initReferenceData() {
      this.$services.iDecisionReferenceData.get({ params: { dataset: 'apartment' } })
        .then(res => this.dataset = res.data)
    },

    ISV(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.$services.iDecisionISV.post('apartment', this.form)
            .then(res => {
              this.alert.show   = false
              this.alert.result = res.data
            })
            .catch(err => {
              this.alert.show  = true
              this.alert.type  = 'error'
              this.alert.title = err
            })
        } else {
          this.alert.show  = true
          this.alert.type  = 'error'
          this.alert.title = 'Error: invalid form'
          return false
        }
      })
    }
  }
}
</script>



