<template>
  <div>
    <el-row :gutter="30">
      <el-alert
        v-if="alert.show"
        :title="alert.title"
        :type="alert.type"
        show-icon
        style="margin-bottom: 2vh" 
        @close="alert.show = false" />
      <pre>{{ alert.result }}</pre>
      
      <el-form 
        :model="form" 
        :rules="rules" 
        :label-position="labelPosition" 
        ref="OCDAutoForm"
        label-width="200px" 
        size="mini">
        <el-col :xs="24" :sm="12" :md="8" :lg="8" :xl="8">
          <el-form-item label="Kinh nghiệm cho thuê xe" prop="car_rental_experience">
            <el-select v-model="form.car_rental_experience">
              <el-option 
                v-for="(o, i) in dataset.car_rental_experience" 
                :key="i"
                :label="o.label" 
                :value="o.id" />
            </el-select>
          </el-form-item>
          
          <el-form-item label="Tình trạng sở hữu xe" prop="car_ownership_status">
            <el-select v-model="form.car_ownership_status">
              <el-option 
                v-for="(o, i) in dataset.car_ownership_status" 
                :key="i"
                :label="o.label" 
                :value="o.id" />
            </el-select>
          </el-form-item>
          
          <el-form-item label="Số km đã đi" prop="number_of_km_traveled">
            <el-select v-model="form.number_of_km_traveled">
              <el-option 
                v-for="(o, i) in dataset.number_of_km_traveled" 
                :key="i"
                :label="o.label" 
                :value="o.id" />
            </el-select>
          </el-form-item>
        </el-col>

        <el-col :xs="24" :sm="12" :md="8" :lg="8" :xl="8">
          <el-form-item label="Giá trị thời điểm xe mới" prop="new_car_value">
            <el-select v-model="form.new_car_value">
              <el-option 
                v-for="(o, i) in dataset.new_car_value" 
                :key="i"
                :label="o.label" 
                :value="o.id" />
            </el-select>
          </el-form-item>

          <el-form-item label="Thời hạn hợp đồng thuê" prop="car_rental_contract_term">
            <el-select v-model="form.car_rental_contract_term">
              <el-option 
                v-for="(o, i) in dataset.car_rental_contract_term" 
                :key="i"
                :label="o.label" 
                :value="o.id" />
            </el-select>
          </el-form-item>
          
          <el-form-item label="Tần suất nhận tiền thuê" prop="car_rental_collection_frequency">
            <el-select v-model="form.car_rental_collection_frequency">
              <el-option 
                v-for="(o, i) in dataset.car_rental_collection_frequency" 
                :key="i"
                :label="o.label" 
                :value="o.id" />
            </el-select>
          </el-form-item>
        </el-col>

        <el-col :xs="24" :sm="12" :md="8" :lg="8" :xl="8">
          <el-form-item prop="car_owner_address">
            <label slot="label">Địa chỉ sinh sống
              <el-tooltip effect="dark" placement="top">
                <div slot="content">
                  Địa chỉ sinh sống của người cho thuê xe
                </div>
                <i class="el-icon-warning" />
              </el-tooltip>
            </label>
            <el-select v-model="form.car_owner_address">
              <el-option 
                v-for="(o, i) in dataset.car_owner_address" 
                :key="i"
                :label="o.label" 
                :value="o.id" />
            </el-select>
          </el-form-item>

          <el-form-item label="Hình thức nhận thu nhập" prop="track_car_rental_receipt">
            <el-select v-model="form.track_car_rental_receipt">
              <el-option 
                v-for="(o, i) in dataset.track_car_rental_receipt" 
                :key="i"
                :label="o.label" 
                :value="o.id" />
            </el-select>
          </el-form-item>
        </el-col>
      </el-form>
    </el-row>
    <el-button plain type="primary" size="small" class="ml-4" @click="OCD('OCDAutoForm')">Test OCD auto</el-button>
  </div>
</template>

<script>
export default {
  name: 'OCD-auto',

  mounted() {
    this.initReferenceData()
  },

  data: () => ({
    labelPosition: 'left',
    // Dataset
    dataset: {},

    form: {
      car_rental_experience: null,
      car_ownership_status: null,
      number_of_km_traveled: null,
      new_car_value: null,
      car_rental_contract_term: null,
      car_rental_collection_frequency: null,
      car_owner_address: null,
      track_car_rental_receipt: null
    },

    rules: {
      car_rental_experience           : [{ required: true, message: 'Vui lòng chọn ', trigger: 'change' }],
      car_ownership_status            : [{ required: true, message: 'Vui lòng chọn ', trigger: 'change' }],
      number_of_km_traveled           : [{ required: true, message: 'Vui lòng chọn ', trigger: 'change' }],
      new_car_value                   : [{ required: true, message: 'Vui lòng chọn ', trigger: 'change' }],
      car_rental_contract_term        : [{ required: true, message: 'Vui lòng chọn ', trigger: 'change' }],
      car_rental_collection_frequency : [{ required: true, message: 'Vui lòng chọn ', trigger: 'change' }],
      car_owner_address               : [{ required: true, message: 'Vui lòng chọn ', trigger: 'change' }],
      track_car_rental_receipt        : [{ required: true, message: 'Vui lòng chọn ', trigger: 'change' }]
    },
    // Alert
    alert: {
      show: false,
      title: null,
      type: null,
      result: null
    }
  }),

  methods: {
    initReferenceData() {
      this.$services.iDecisionReferenceData.get({ params: { dataset: 'OCD_auto' } })
        .then(res => this.dataset = res.data)
    },

    OCD(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.$services.iDecisionOCD.post('auto', this.form)
            .then(res => {
              this.alert.show   = false
              this.alert.result = res.data
            })
            .catch(err => {
              this.alert.show  = true
              this.alert.type  = 'error'
              this.alert.title = err
            })
        } else {
          this.alert.show  = true
          this.alert.type  = 'error'
          this.alert.title = 'Error: invalid form'
          return false
        }
      })
    }
  }
}
</script>



