<template>
  <el-collapse v-model="activePanels">
    <el-collapse-item 
      v-for="(c, i) in components"
      :key="i"
      :title="c.title" 
      :name="i">
      <component :is="c.component" />
    </el-collapse-item>
  </el-collapse>
</template>

<script>
import OCDSalary from '@/components/iDecision/Test/OCD/Salary'
import OCDAuto from '@/components/iDecision/Test/OCD/Auto'
import OCDHouseholdBusiness from '@/components/iDecision/Test/OCD/Household'
import OCDRealEstate from '@/components/iDecision/Test/OCD/RealEstate'

import RSVAuto from '@/components/iDecision/Test/RSV/Auto'
import ISVGround from '@/components/iDecision/Test/ISV/Ground'
import ISVApartment from '@/components/iDecision/Test/ISV/Apartment'
import ISVResidentialLand from '@/components/iDecision/Test/ISV/ResidentialLand'

export default {
  name: 'idecision-test',

  components: {
    OCDAuto,
    OCDSalary,
    OCDHouseholdBusiness,
    OCDRealEstate,
    RSVAuto,
    ISVGround,
    ISVApartment,
    ISVResidentialLand
  },

  data: () => ({
    activePanels: [],

    components: [
      { title: 'OCD và khung lương', component: 'OCDSalary' },
      { title: 'OCD ôtô', component: 'OCDAuto' },
      { title: 'OCD HKD', component: 'OCDHouseholdBusiness' },
      { title: 'OCD BĐS', component: 'OCDRealEstate' },
      // { title: 'RSV ôtô', component: 'RSVAuto' },
      // { title: 'ISV đất nền', component: 'ISVGround' },
      // { title: 'ISV đất thổ cư', component: 'ISVResidentialLand' },
      { title: 'ISV căn hộ', component: 'ISVApartment' }
    ]
  })
}
</script>
<style lang="css" scoped>
>>>.el-collapse-item__header {
  padding-left: 15px;
  padding-right: 15px;
  font-size: 15px;
} 
>>>.el-input-number .el-input__inner {
  text-align: left;
}
>>>.el-row {
  margin-left: 10px !important;
  margin-right: 30px !important;
  &:last-child {
    margin-bottom: 0;
  }
}
>>>.grid-content {
  border-radius: 4px;
  min-height: 36px;
}
>>>.el-select {
  width: 100%;
}
</style>



